import React, { useState, useEffect } from "react";
import Todo from "./todo";
import TotalTodos from "./Total";

const ShowInput = ({ todos, setTodos }) => {
  const [totalItems, setTotalItems] = useState(1);

  useEffect(() => {
    const totalItem = todos.reduce((todo, item) => {
      return todo + item.totalQuantity;
    }, 0);
    setTotalItems(totalItem);
  }, [todos]);

  return (
    <div>
      <table className="show-results">
        <tbody>
          {todos.map((todo) => (
            <Todo
              key={todo.id}
              text={todo.text}
              todo={todo}
              todos={todos}
              setTodos={setTodos}
            />
          ))}
        </tbody>
      </table>
      <TotalTodos totalItems={totalItems} />
    </div>
  );
};

export default ShowInput;
