import React from "react";

const Todo = ({ text, todos, todo, setTodos }) => {
  // Delete Function
  const deleteHandler = () => {
    if (window.confirm("Are you sure?")) {
      setTodos(todos.filter((el) => el.id !== todo.id));
    }
  };

  // Complete Checkout Function
  const completeHandler = () => {
    setTodos(
      todos.map((item) => {
        if (item.id === todo.id) {
          return {
            ...item,
            completed: !item.completed,
            totalQuantity: 0,
          };
        }
        return item;
      })
    );
  };

  // Quantity Increase Function
  const incHandler = () => {
    setTodos(
      todos.map((item) => {
        if (item.id === todo.id) {
          item.totalQuantity++;
        }
        return item;
      })
    );
  };

  // Quantity Decrease Function
  const decHandler = () => {
    if (todo.totalQuantity > 1) {
      setTodos(
        todos.map((item) => {
          if (item.id === todo.id) {
            item.totalQuantity--;
          }
          return item;
        })
      );
    } else {
      alert(" Zero is values not allowed ");
    }
  };

  // Return Statements
  return (
    <tr>
      <td className={`show-input ${todo.completed ? "completed" : ""}`}>
        {text}
      </td>
      <td className="quantity">
        <div>
          <i
            onClick={decHandler}
            className="fa fa-chevron-left"
            aria-hidden="true"
          ></i>
          <span>{todo.totalQuantity}</span>
          <i
            onClick={incHandler}
            className="fa fa-chevron-right"
            aria-hidden="true"
          ></i>
        </div>
      </td>
      <td onClick={completeHandler} className="checkout">
        <i className="fa fa-check" aria-hidden="true"></i>
      </td>
      <td onClick={deleteHandler} className="trash">
        <i className="fa fa-trash" aria-hidden="true"></i>
      </td>
    </tr>
  );
};

export default Todo;
